import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import HomeScreen from "./components/homeScreen";
import MainScreen from "./components/main";
import "./style/app.css";

function App() {
	return (
		<div className="app">
			<Router>
				<Switch>
					<Route path="/main">
						<MainScreen />
					</Route>
					<Route path="/">
						<HomeScreen />
					</Route>
				</Switch>
			</Router>
		</div>
	);
}

export default App;
